@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  background-color: #282c34;
  color: white;
  font-family: 'Noto Sans JP', sans-serif;
}

a {
  text-decoration: none;
}

.article__container {
  margin: 24px auto;
  width: 88vw;
  border: 1px solid rgba(255, 255, 255, 0.200);
  display: flex;
  height: 260px;
  border-radius: 8px;
}

.article__section-image {
  border-radius: 8px;
}

.article__section-image img {
  object-fit: cover;
  width: 350px;
  height: 100%;
  border-radius: 8px 0 0 8px;
  border-right: 1px solid rgba(255, 255, 255, 0.200);
}

.article__section-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0px 24px 18px;
  width: 100%;
}

.article__section-footer {
  display: flex;
  justify-content: space-between;
}

.article__section-social {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
  background-color: #3077b5;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
}

.article__section-social img {
  width: 2vw;
  background-color: #3077b5;
}

@media screen and (max-width: 998px) {
  .article__container {
    flex-direction: column;
    height: 100vh;
    justify-content: space-between;
    margin-bottom: 45px;
  }

  .article__section-image {
    width: 100%;
    border-radius: 8px 8px 0 0;
  }

  .article__section-image img {
    width: 88vw;
    height: 30vh;
    border-radius: 8px 8px 0 0;
  }

  .article__section-social {
    border-radius: 0 0 8px 8px;
  }

  .article__section-social img {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }

  .article__section-footer {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
}
.header__section {
  background-color: #f68b01;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  width: 100vw;
}

.header__section img {
  height: 40px;
  margin: 5px 0 5px 0;
}

.home__select-country {
  width: 100%;
  display: flex;
  justify-content: center;
}

.home__select-country img {
  cursor: pointer;
  height: 30px;
  margin: 0 18px;
  border-radius: 50%;
}

.home__content-section {
  margin-top: 60px;
}

footer {
  display: flex;
  justify-content: center;
  border-top: 1px solid #f68c015b;
  height: 20px;
  font-size: small;
  padding: 8px;
}
